  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.header h1{

  width: 20%;

  text-align: center;
}
.header nav {

  height: 100%;

  width: 45%;

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;

  -ms-flex-pack: distribute;

      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 35px;
}
.header nav button{

  color: #D0DCD0;

  background: #434643;

  border: 2px solid #0000;

  border-radius: 5px;

  padding: 10px 20px;

  font-size: 13px;
  font-weight: bold;
}
.header nav button:hover{

  color: #434643;

  background: #6BDEBB;

  border: 2px solid #434643;
}
