#logo {

    width: -10%;

    font-size: 30px;

    float: left;

    margin: 4px 5px 6px 20px;
}
#logo2 {

    width: -1%;

    font-size: 15px;

    float: left;

    margin: 4px 5px 6px -479px;
}
#logo img {

    display: block;

    float: left;

    height: 60px;

    margin-top: -9px;

    margin-right: 1px;

    -webkit-animation: girar infinite 30s linear;

            animation: girar infinite 30s linear;
}



/*Creando animacion*/
@-webkit-keyframes girar {
from {

        -webkit-transform: rotate(0deg);

                transform: rotate(0deg);
}
to {

        -webkit-transform: rotate(360deg);

                transform: rotate(360deg);
}
}
@keyframes girar {
from {

        -webkit-transform: rotate(0deg);

                transform: rotate(0deg);
}
to {

        -webkit-transform: rotate(360deg);

                transform: rotate(360deg);
}
}


